
















/* eslint-disable @typescript-eslint/explicit-module-boundary-types */
import Tags from "@/components/Money/Tags.vue";
import Types from "@/components/Money/Types.vue";
import Pad from "@/components/Money/Pad.vue";
import FormItem from "@/components/Money/FormItem.vue";
import Datechoose from "@/components/Datechoose.vue";
import Vue from "vue";
import { Component } from "vue-property-decorator";
import dayjs from "dayjs";
import { Notify } from "vant";

@Component({
  components: { Tags, Types, Pad, FormItem, Datechoose },
})
export default class Money extends Vue {
  ThisDate = dayjs(new Date()).format("YYYY/M/D");
  record: RecordItem = { tags: [], notes: "", type: "-", amount: 0 };

  get recordList() {
    return this.$store.state.recordList;
  }
  get tags() {
    return this.$store.state.tagList.filter(
      (r: any) => r.type === this.record.type
    );
  }
  created() {
    this.$store.commit("fetchRecords");
    this.$store.commit("fetchTags");
  }

  onUpdateTags(value: Tag[]) {
    this.record.tags = value;
  }
  onUpdateNotes(value: string) {
    if (value === "") {
      this.record.notes = "无备注";
    } else {
      this.record.notes = value;
    }
  }
  saveRecord() {
    if (this.record.notes === "") {
      this.record.notes = "无备注";
    }
    if (this.record.tags[0] && this.record.tags[0].type === this.record.type) {
      this.$store.commit("createRecord", {
        createdDate: this.ThisDate,
        record: this.record,
      });
    } else {
      Notify({ type: "warning", message: "请至少选择一个标签/创建新标签" });
    }
  }
}
